#villas {
  position: relative;
  padding-top: 340px;
  &::before {
    @include addBgStyling(340px, top, #000);
    @media(min-width: $atLaptopsMd) {
      height: 535px; } }
  &::after {
    @include addBgStyling(360px, bottom, #000);
    @media(min-width: $atLaptopsMd) {
      height: 310px; } }

  .container_no_r_pad {
    padding-left: 8%;
    overflow: hidden;
    .carousel_control {
      padding-left: 35px;
      &::before {
        content: '';
        display: block;
        overflow: hidden; } }
    @media(min-width: $atLaptopsMd) {
      display: flex;
      .carousel_control {
        flex: 1 0 400px;
        padding-left: 0; } } }

  .container_no_l_pad {
    padding-right: 8%;
    h3, .slider_nav, .sub_head {
      margin-right: 35px;
      text-align: right; }
    h3 {
      margin-right: .19em; }
    .sub_head {
      margin-right: .875em; }
    @media(min-width: $atLaptopsMd) {
      display: flex;
      .carousel_control {
        flex: 1 0 400px;
        padding-left: 0;
        order: 2;
        position: relative;
        left: -400px; }
      .owl-carousel {
        left: -400px; } } }

  h2 {
    display: none;
    &::after {
      left: 10%; }
    @media(min-width: $atLaptopsMd) {
      display: block;
      margin-bottom: 120px; } }

  h3 {
    color: #000;
    font-size: 48px;
    text-transform: uppercase;
    letter-spacing: .625em;
    font-weight: 700;
    margin-bottom: 15px;
    @media(min-width: $atLaptopsMd) {
      font-size: 30px;
      margin-top: 80px; } }

  .slider_nav {
    font-family: $font-primary;
    font-size: 25px;
    font-weight: 400;
    color: #000;
    margin-bottom: 40px;
    #rentAllNum, #buyAllNum {
      color: $dark-gold; }
    #rentNext, #rentPrev, #buyNext, #buyPrev {
      font-family: 'Courier New', monospace;
      cursor: pointer; }
    #rentNext, #buyNext {
      margin-left: 30px; }
    #rentPrev, #buyPrev {
      margin-right: 30px; }
    @media(min-width: $atLaptopsMd) {
      margin-top: 125px; } }
  .sub_head {
    color: rgba(#000, .4);
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: .625em;
    margin-bottom: 26px;
    @media(min-width: $atLaptopsMd) {
      font-size: 17px; } }

  .villa_view {
    position: relative;
    img {
      object-fit: cover;
      @media(min-width: $atPhoneLg) {
        width: 558px; }
      @media(min-width: $atLaptopsMd) {
        height: 347px;
        width: 758px; } }

    &:before {
      content: '';
      position: absolute;
      height: 100%;
      width: 100%;
      bottom: 0;
      background: url(data:image/png;base64,) repeat-x bottom left; }

    .villa_name {
      position: absolute;
      left: 0;
      bottom: 1.5em;
      font-family: $font-secondary;
      font-weight: 400;
      text-transform: uppercase;
      color: #fff;
      font-size: 19px;
      padding-left: 2em;
      span {
        color: #a08f6e;
        font-size: 15px; } } }

  .villa_details {
    padding: .54em;
    margin-top: .68em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    color: rgba(#333, .5);
    font-family: $font-primary;
    font-size: 18px;
    @media(min-width: $atPhoneLg) {
      font-size: 22px;
      &:after {
        content: '';
        flex-basis: 100%; } }
    span {
      display: inline-block;
      padding-top: .63em;
      padding-bottom: 1em;
      margin-bottom: .5em;
      text-transform: capitalize;
      &:nth-child(2) {
        border-right: 2px solid #f2f2f2;
        border-left: 2px solid #f2f2f2;
        padding-left: 1.3em;
        padding-right: 1.3em; } }
    a {
      display: inline-block;
      background-color: #000;
      color: #fff;
      font-family: $font-secondary;
      font-size: .77em; //17px
      text-transform: uppercase;
      padding: 1.29em 1.4em;
      text-decoration: none;
      order: 1;
      margin-left: auto; } } }

