.select_hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px; }

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 16px;
  color: #000;
  width: 100%;
  max-width: 250px;
  height: 45px; }

.select_styled {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #fff;
  padding: 8px 10px;
  line-height: 30px;
  text-transform: capitalize;
  transition: all 0.2s ease-in; }
  .select_styled:after {
    content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="9" viewBox="0 0 16 9"><path d="M8.17 6.65L14.69.13l1.03 1.05-7.53 7.53h-.02L.64 1.18 1.67.13z"/></svg>');
    width: 15px;
    height: 9px;
    position: absolute;
    right: 10px; }
  .select_styled:hover {
    background-color: #f5f5f5; }
  .select_styled:active, .select_styled.active {
    background-color: #f5f5f5; }

.select_options {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #fff;
  border-bottom: 5px solid transparent; }
  .select_options li {
    margin: 0;
    padding: 12px 0;
    text-indent: 15px;
    transition: all 0.15s ease-in;
    border-left: 5px solid #fff;
    border-right: 5px solid #fff;
    text-transform: capitalize; }
    .select_options li:hover {
      color: #fff;
      background-color: #a08f6e; }
    .select_options li[rel="hide"] {
      display: none; }
    .select_options li[rel="disabled"] {
      color: #c8c8c8;
      font-size: 14px;
      text-transform: uppercase; }
      .select_options li[rel="disabled"]:hover {
        background-color: transparent;
        cursor: default; }
